@import './button';
@import './text';

* {
    background: inherit;
	padding: 0;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	color: inherit;
	text-align: center;
	word-wrap: break-word;
	scroll-behavior: smooth;
}


$accent-color: #ee1044;
$primary-color: #d43f62;
$tertiary-color: #3fd48f;
$secondary-color: #acd43f;

%flex-col {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
}

button {
    color: $accent-color;
    border-color: $accent-color;
}

.material-icons {
	transition: transform 0.5s;

	&:hover {
		transform: scale(1.2);
	}
	&:active {
		transform: scale(2);
	}
}