body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button{background:white;border:3px solid black;border-radius:6px;color:black;font-size:1.2rem;font-weight:bold;padding:0.5rem 1rem;transition:transform 0.5s;text-transform:uppercase}button:hover{transform:scale(1.05);cursor:pointer}button:active{transform:scale(1.2)}@media only screen and (max-width: 1024px){button{transform:scale(0.9)}}@media only screen and (max-width: 768px){button{transform:scale(0.8)}}h1,h2,h3,h4,h5,h6{margin:1vh 1vw 2vh 1vw}p{font-size:1.2rem;margin-bottom:2vh}@media only screen and (max-width: 1024px){h1,h2,h3,h4,h5,h6,p{transform:scale(.9)}}@media only screen and (max-width: 768px){h1,h2,h3,h4,h5,h6,p{transform:scale(.8)}}*{background:inherit;padding:0;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;color:inherit;text-align:center;word-wrap:break-word;scroll-behavior:smooth}.intro,.about,.projects{align-items:center;display:flex;flex-direction:column;justify-content:center;margin:0 auto}button{color:#ee1044;border-color:#ee1044}.material-icons{transition:transform 0.5s}.material-icons:hover{transform:scale(1.2)}.material-icons:active{transform:scale(2)}#block1{position:absolute;top:9vh;right:4vw;width:16vmin;height:16vmin;background:#ee1044;opacity:0.2}#block2{position:absolute;top:6vh;right:8vw;width:15vmin;height:15vmin;background:#acd43f;opacity:0.1}.intro{width:50%;height:100vh;font-size:120%;border-bottom:1px solid #aaa}header{align-content:space-between;background:white;border-bottom:1px solid rgba(127,127,127,0.5);display:flex;flex-direction:row;height:minmax(42px, auto);position:-webkit-sticky;position:sticky;top:0;z-index:10}.home-portal{font-size:1.2rem;font-weight:400;letter-spacing:0.05rem;margin:auto calc(1rem + 2vw)}.home-portal a{text-decoration:none}.home-portal span{transition:color 0.5s ease-in-out, font-size 1s ease-in-out, font-weight 0.5s ease-in-out}.home-portal:hover span{font-size:1.25rem;font-weight:600}.home-portal:hover span:nth-child(1){color:#d43f62}.home-portal:hover span:nth-child(2){color:#acd43f}.home-portal:hover span:nth-child(3){color:#3fd48f}.home-portal:hover span:nth-child(4){color:#4b48e0}nav{padding:0 calc(1rem + 5vw);text-align:right;width:100%}nav a{border-bottom:2px solid white;display:inline-block;font-weight:600;padding:11px 2vw 9px 2vw;text-decoration:none;transition:background 0.5s, color 0.5s}nav a:hover{background:#d43f62;color:white;transition:background 0.5s}@media only screen and (max-width: 768px){.home-portal{display:none}nav{text-align:center}}.about{background:#d43f62;color:white;padding:10vh 10vw}.about .material-icons{color:white;font-size:200%;padding:2vh 1vw 1vh 1vw;cursor:default}.skills{padding:5vh 20vw 5vh 10vw}.skills *{transform-origin:left}.projects-banner{background:#d43f62;color:white;border-top:5px solid #3fd48f;margin-bottom:10vh;padding:2vh 0;width:100%}.social img{margin:1vh 1vw;transition:transform 0.5s}.social img:hover{transform:translate3d(0px, -10px, 0px);transition:transform 0.5s}.social img:active{transform:scale(1.2);transition:transform 0.5s}footer{border-top:5px solid #d43f62;padding:5rem}footer .imprint-toggle{text-decoration:underline;cursor:pointer}footer .imprint-content{padding-top:4vh;font-size:0.75rem}.project-card{background:white;border:1px solid #ccc;border-radius:4px;display:grid;font-size:80%;grid-template-rows:1fr;grid-template-columns:1fr 5fr;transition:transform 0.5s;width:70vw}.project-card:hover{transform:scale(1.05);transition:transform 0.5s}.project-card:active{transform:scale(1.06);transition:transform 0.1s}.project-card-left{min-width:200px;border-right:1px solid #e4e4e4}.project-card-left img{height:auto;width:100%}.project-card-main{padding:3vh 2vw}.project-card-main *{text-align:justify}.project-card-main .mainDesc h3{margin:1vh 0}.project-card-main .mainDesc p{font-size:1rem}.project-card-main .bottom{display:grid;grid-template-columns:1fr auto;padding-top:1rem}.project-card-main .bottom .meta{font-style:italic;opacity:0.7}@media only screen and (max-width: 1024px){.project-card-main .mainInfo,.project-card-main .bottom{transform:scale(0.9)}}@media only screen and (max-width: 768px){.project-card{grid-template-rows:auto 1fr;grid-template-columns:1fr}.project-card-left{border-bottom:1px solid #e4e4e4}.project-card-main .mainInfo,.project-card-main .bottom{transform:scale(0.8)}}.skill-card{display:grid;font-size:120%;grid-template:1fr / 1fr 4fr;padding:5vh 0}.skill-card *{margin:0;text-align:left}.skill-card h2{padding:1vh 0}.skill-card i{font-size:3rem;padding:4vmin}.skill-card:nth-child(3n+1) h2,.skill-card:nth-child(3n+1) i{color:#d43f62}.skill-card:nth-child(3n+2) h2,.skill-card:nth-child(3n+2) i{color:#acd43f}.skill-card:nth-child(3n) h2,.skill-card:nth-child(3n) i{color:#3fd48f}.tag-item :hover,.tag-item.alternate:hover,.tag-item.primary,.tag-item.tertiary:hover{color:#d43f62;border:2px solid #d43f62}.tag-item ,.tag-item.primary:hover,.tag-item.secondary{color:#acd43f;border:2px solid #acd43f}.tag-item.alternate,.tag-item.secondary:hover,.tag-item.tertiary{color:#3fd48f;border:2px solid #3fd48f}.tag-item{border-radius:6px;display:inline-block;font-size:0.8rem;font-weight:bold;margin:0.5vmin;padding:0.3rem 0.6rem;transition:color 0.5s, border 0.5s}.tag-item:hover{cursor:default}

